import React from "react";
import PortfolioLayout from "../../../Components/PortfolioLayout";

export default function ApexLegendsSteamSentimentAnalysis() {
  return (
    <PortfolioLayout>
      {(project) => (
        <>
          <h4 className="introduction">
             SteamApex Legends
          </h4>
          <p>
            Apex Legends Steam
             EA
            
            CoreNLPBERT
            20244711 battle
            pass
            matchmakinganticheatLinux
             LDA
            
            
          </p>

          <hr className="section-divider" />
          <h4></h4>
          <p>
            
          </p>

          <h5>1. </h5>
          <ul>
            <li> Steam </li>
            <li>
              
            </li>
            <li></li>
          </ul>

          <h5>2. </h5>
          <ul>
            <li> Bigram </li>
            <li>
               Pairwise correlation
              matchmakinglinux
            </li>
          </ul>

          <h5>3. </h5>
          <ul>
            <li>
               BERT
              
            </li>
            <li>
               Logistic Regression
              
            </li>
            <li></li>
          </ul>

          <h5>4. </h5>
          <ul>
            <li>
               LDA 
            </li>
            <li>
              
            </li>
          </ul>

          <hr className="section-divider" />
          <h4></h4>
          <div className="image-gallery">
            <div className="image-gallery">
              <div style={{ marginBottom: "2rem" }}>
                <a
                  href={project.pdf}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="pdf-link"
                >
                  
                </a>
                <iframe
                  src="/ApexLegendsSteamSentimentAnalysis.html"
                  width="100%"
                  height="1200px"
                  style={{ border: "none" }}
                  title="Apex Legends Sentiment Analysis Report"
                />
              </div>
              {project.images?.map((img, index) => {
                let imageSrc;
                try {
                  imageSrc = require(`../../../images/${img.path}`);
                } catch (e) {
                  console.warn(":", img.path);
                  imageSrc = require("../../../images/default.png"); // 
                }

                return (
                  <div key={index}>
                    <h5>{img.title}</h5>
                    <p>{img.content}</p>
                    <img
                      src={imageSrc}
                      alt={img.title}
                      className="gallery-image"
                      data-zoomable="true"
                    />{" "}
                  </div>
                );
              })}
            </div>
          </div>
        </>
      )}
    </PortfolioLayout>
  );
}
