import React from "react";
import PortfolioLayout from "../../../Components/PortfolioLayout";

export default function XuanWeiDevelopment() {
  return (
    <PortfolioLayout>
      {(project) => (
        <>
          <h4 className="introduction">
              B2B 
          </h4>
          <p>
             95
            
            
          </p>

          <hr className="section-divider" />
          <h4></h4>
          <p>
            
            SEO
          </p>

          <ul>
            {" "}
            <li></li>{" "}
            <li>
               Banner
            </li>{" "}
            <li>
               GA  SEO 
            </li>{" "}
            <li>
               Google TrendUbersuggest 
            </li>{" "}
          </ul>

          <hr className="section-divider" />
          <h4></h4>
          <div className="image-gallery">
            <div className="image-gallery">
              {project.images?.map((img, index) => {
                let imageSrc;
                try {
                  imageSrc = require(`../../../images/${img.path}`);
                } catch (e) {
                  console.warn(":", img.path);
                  imageSrc = require("../../../images/default.png"); // 
                }

                return (
                  <div key={index}>
                    <h5>{img.title}</h5>
                    <img
                      src={imageSrc}
                      alt={img.title}
                      className="gallery-image"
                      data-zoomable="true"
                    />
                  </div>
                );
              })}
            </div>
          </div>
        </>
      )}
    </PortfolioLayout>
  );
}
