import React from "react";
import PortfolioLayout from "../../../Components/PortfolioLayout";

export default function DiamondPricePrediction() {
  return (
    <PortfolioLayout>
      {(project) => (
        <>
          <h4 className="introduction">
            
          </h4>
          <p>
             Kaggle  Diamonds
            
            SVMKNN
            AdaBoostBaggingStackingF1
             AUC 
          </p>

          <hr className="section-divider" />
          <h4></h4>
          <p>
            
          </p>
          <ul>
            <li>
              <strong></strong>
              / 7:3
            </li>
            <li>
              <strong></strong>
              Logistic RegressionSVMKNNDecision TreeRandom Forest
            </li>
            <li>
              <strong></strong>
              Grid SearchLearning CurveValidation CurveEnsemble
              LearningAdaBoostBaggingStacking
            </li>
            <li>
              <strong></strong>
              AccuracyPrecisionRecallF1 ScoreAUC
            </li>
          </ul>

          <hr className="section-divider" />
          <h4></h4>
          <div className="image-gallery">
            <div className="image-gallery">
              <div style={{ marginBottom: "2rem" }}>
                <a
                  href={project.pdf}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="pdf-link"
                >
                  
                </a>
              </div>
              {project.images?.map((img, index) => {
                let imageSrc;
                try {
                  imageSrc = require(`../../../images/${img.path}`);
                } catch (e) {
                  console.warn(":", img.path);
                  imageSrc = require("../../../images/default.png"); // 
                }

                return (
                  <div key={index}>
                    <h5>{img.title}</h5>
                    <p>{img.content}</p>
                    <img
                      src={imageSrc}
                      alt={img.title}
                      className="gallery-image"
                      data-zoomable="true"
                    />{" "}
                  </div>
                );
              })}
            </div>
          </div>
        </>
      )}
    </PortfolioLayout>
  );
}
