import React from "react";
import PortfolioLayout from "../../../Components/PortfolioLayout";

export default function HealthManagementApp() {
  return (
    <PortfolioLayout>
      {(project) => (
        <>
          <h4 className="introduction">
             App  
          </h4>
          <p>
            
            AppBMRTDEE
          </p>

          <hr className="section-divider" />
          <h4></h4>
          <p>
            
            App
          </p>
          <li>
            React Native  App React Navigation
            
          </li>
          <li>
            Node.jsExpressBMR/TDEE  API
          </li>
          <li>
            MySQL 
          </li>

          <hr className="section-divider" />
          <h4></h4>
          <div className="image-gallery">
            <div className="image-gallery">
              {project.images?.map((img, index) => {
                let imageSrc;
                try {
                  imageSrc = require(`../../../images/${img.path}`);
                } catch (e) {
                  console.warn(":", img.path);
                  imageSrc = require("../../../images/default.png"); // 
                }

                return (
                  <div key={index}>
                    <h5>{img.title}</h5>
                    <p>{img.content}</p>
                    <img
                      src={imageSrc}
                      alt={img.title}
                      className="gallery-image"
                      data-zoomable="true"
                      style={{ maxWidth: "25em", height: "auto" }}
                    />
                  </div>
                );
              })}
            </div>
          </div>
        </>
      )}
    </PortfolioLayout>
  );
}
