import React from "react";
import PortfolioLayout from "../../../Components/PortfolioLayout";

export default function OrderAnalysis() {
  return (
    <PortfolioLayout>
      {(project) => (
        <>
          <h4 className="introduction">
              
          </h4>
          <p>
             Python  RFM 
            
             scikit_learn  metrics.
            Pairwise  <code>cosine_similarity</code>{" "}
              array
            
          </p>

          <hr className="section-divider" />
          <h4></h4>
          <p>
             Python 
          </p>
          <ul>
            {" "}
            <li>pandas </li>{" "}
            <li>RFM K-means </li>{" "}
            <li>
              scikit-learn  <code>cosine_similarity</code>{" "}
              
            </li>{" "}
            <li></li>{" "}
            <li> Apriori </li>{" "}
          </ul>

          <hr className="section-divider" />
          <h4></h4>
          <div className="image-gallery">
            <div className="image-gallery">
              <div style={{ marginBottom: "2rem" }}>
                <a
                  href={project.pdf}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="pdf-link"
                >
                  
                </a>
              </div>
              {project.images?.map((img, index) => {
                let imageSrc;
                try {
                  imageSrc = require(`../../../images/${img.path}`);
                } catch (e) {
                  console.warn(":", img.path);
                  imageSrc = require("../../../images/default.png"); // 
                }

                return (
                  <div key={index}>
                    <h5>{img.title}</h5>
                    <p>{img.content}</p>
                    <img
                      src={imageSrc}
                      alt={img.title}
                      className="gallery-image"
                      data-zoomable="true"
                    />{" "}
                  </div>
                );
              })}
            </div>
          </div>
        </>
      )}
    </PortfolioLayout>
  );
}
